// src/services/api.js
import axios from "axios";

// Configuration de base pour l'API
const API_BASE_URL = import.meta.env.PROD
  ? window.location.origin // En production, m√™me domaine
  : "http://localhost:8888"; // En d√©veloppement local avec Netlify Dev

// Instance axios configur√©e
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 30000, // 30 secondes pour les fonctions Netlify
  headers: {
    "Content-Type": "application/json",
  },
});

// Intercepteur pour les erreurs
api.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error("API Error:", error);

    // Gestion des erreurs r√©seau
    if (!error.response) {
      throw new Error("Probl√®me de connexion r√©seau");
    }

    // Gestion des erreurs serveur
    if (error.response.status >= 500) {
      throw new Error("Erreur serveur. Veuillez r√©essayer plus tard.");
    }

    throw error;
  }
);

// Service API pour les √©v√©nements
export const eventService = {
  // R√©cup√©rer tous les √©v√©nements
  async getEvents() {
    try {
      const response = await api.get("/api/events");
      return response.data;
    } catch (error) {
      console.error("Erreur lors de la r√©cup√©ration des √©v√©nements:", error);
      throw error;
    }
  },
};

// Service API pour les codes promo
export const promoService = {
  // Valider un code promo
  async validatePromo(code, nombreBillets) {
    try {
      const response = await api.post("/api/validate-promo", {
        code,
        nombreBillets,
      });
      return response.data;
    } catch (error) {
      console.error("Erreur validation code promo:", error);
      throw error;
    }
  },
};

// Service API pour les tickets
export const ticketService = {
  // Cr√©er un nouveau ticket
  async createTicket(ticketData) {
    try {
      const response = await api.post("/api/tickets", ticketData);
      return response.data;
    } catch (error) {
      console.error("Erreur cr√©ation ticket:", error);
      throw error;
    }
  },

  // Valider un ticket (admin)
  async validateTicket(ticketId, action) {
    try {
      const response = await api.put(`/api/tickets/${ticketId}/validate`, {
        action, // 'approve' ou 'reject'
      });
      return response.data;
    } catch (error) {
      console.error("Erreur validation ticket:", error);
      throw error;
    }
  },

  // V√©rifier un ticket √† l'entr√©e
  async verifyTicket(verificationData) {
    try {
      const response = await api.post("/api/tickets/verify", verificationData);
      return response.data;
    } catch (error) {
      console.error("Erreur v√©rification ticket:", error);
      throw error;
    }
  },
};

// Service API pour les paiements Mobile Money
export const paymentService = {
  // Simulation paiement Mobile Money
  async simulateMobileMoneyPayment(paymentData) {
    try {
      const response = await api.post(
        "/api/mobile-money/simulate",
        paymentData
      );
      return response.data;
    } catch (error) {
      console.error("Erreur simulation Mobile Money:", error);
      throw error;
    }
  },

  // Webhook Mobile Money (pour les int√©grations r√©elles)
  async handleMobileMoneyWebhook(webhookData) {
    try {
      const response = await api.post("/api/webhook/mobile-money", webhookData);
      return response.data;
    } catch (error) {
      console.error("Erreur webhook Mobile Money:", error);
      throw error;
    }
  },
};

// Service API pour l'administration
export const adminService = {
  // R√©cup√©rer les statistiques admin
  async getStats() {
    try {
      const response = await api.get("/api/admin/stats");
      return response.data;
    } catch (error) {
      console.error("Erreur r√©cup√©ration stats:", error);
      throw error;
    }
  },
};

// Utilitaires
export const apiUtils = {
  // Test de connectivit√© API
  async testConnection() {
    try {
      const response = await api.get("/api/events");
      return { success: true, data: response.data };
    } catch (error) {
      return { success: false, error: error.message };
    }
  },

  // R√©cup√©rer l'URL de base de l'API
  getBaseURL() {
    return API_BASE_URL;
  },
};

export default api;
